import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Filter from './views/Filter.vue'
import MySells from './views/MySells.vue'
import New from './views/New/index.vue'
import NewJewel from './views/New/NewJewel.vue'
import NewSeller from './views/New/NewSeller.vue'
import NewCategory from './views/New/NewCategory.vue'
import NewSale from './views/New/NewSale.vue'
import Analytics from './views/Analytics.vue'
import Configs from './views/Configs.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/jewel-filter',
      name: 'JewelsFilter',
      component: Filter
    },
    {
      path: '/my-sells',
      name: 'my-sells',
      component: MySells
    },
    {
      path: '/new',
      name: 'New',
      component: New,
      children: [
        {
          path: 'jewel',
          name: 'NewJewel',
          component: NewJewel
        },
        {
          path: 'seller',
          name: 'NewSeller',
          component: NewSeller
        },
        {
          path: 'category',
          name: 'NewCategory',
          component: NewCategory
        },
        {
          path: 'sale/:uuid',
          name: 'NewSale',
          component: NewSale
        }
      ]
    },
    {
      path: '/analytics',
      name: 'analytics',
      component: Analytics
    },
    {
      path: '/config',
      name: 'config',
      component: Configs
    }
  ]
})
